/* add styles here */
body {
  margin: 0;
}

.stars {
  display: flex;
}

.stars__star {
  width: 16px;
  height: 16px;
  background-image: url(images/star.svg);
  background-repeat: no-repeat;
  background-position: center;
  margin-right: 4px;
}

.stars--1 :first-child,
.stars--2 :nth-of-type(-n+2),
.stars--3 :nth-of-type(-n+3),
.stars--4 :nth-of-type(-n+4),
.stars--5 :nth-of-type(-n+5) {
  background-image: url(images/star-active.svg);
}
